.application,
body {
  background: $background;
  color: $color;
  font-family: $font-normal;
  font-weight: 300;
  height: 100vh;
  width: 100vw;
  margin: 0;
}

a {
  color: $active;
  text-decoration: none;
}

.button {
  background: $active;
  color: $color-alt;
  border-radius: $radius-normal;
  padding: $spacing-large $spacing-normal;
  margin: $spacing-normal;
}

.navBar {
  background: $active-medium;
  color: $color;
  flex: none;
  box-shadow: $box-shadow;
  &.sideNav {
    background: $active-dark;
    text-align: right;
    a {
      color: $color;
      &:hover {
        background: $highlight;
      }
      &:active {
        background: $highlight;
        box-shadow: inset 0 $spacing-small 0 $active-dark;
      }
    }
  }

  .button,
  a {
    color: $color;
    background: transparent;
    padding: $spacing-large $spacing-normal;
    margin: 0;
    border-radius: 0;
    text-transform: uppercase;
    font-weight: 400;
    &:hover {
      background: $highlight;
    }
    &:active {
      background: $highlight;
      font-weight: 600;
      box-shadow: inset 0 $spacing-extra-small 0 $active-dark;
    }
  }
  a.logo {
    padding: 0 $spacing-large;
    display: flex;
    img {
      height: 2.25rem;
      align-self: center;
    }
  }
}

.sectionHeader {
  font-weight: 600;
  font-size: 2rem;
  background: $highlight;
  padding: $spacing-small;
  box-shadow: $box-shadow;
}

.cardContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex: none;
  justify-content: space-between;
}

.card {
  &.jobCard {
    min-width: 320px;
    width: 33%;
  }
  .cardContent {
    background: $background-alt;
    color: $color-alt;
    border-radius: $radius-normal;
    font-size: .9rem;
    padding: $spacing-small;
    margin: $spacing-small;
    flex: none;
    overflow: hidden;
    box-shadow: $box-shadow;
    .jobType {
      font-weight: 400;
      font-size: 1rem;
      margin: -.5vmax 0 $spacing-extra-small -.5vmax;
      padding: $spacing-small;
      flex-grow: 2;
      display: flex;
      width: 105%;
      background: $action;
      color: $color;
      &.pg {
        background: $pg;
        color: $color-alt;
      }
      &.um {
        background: $um;
      }
    }
    .itemTag, .date, .time {
      font-size: .8rem;
      border-radius: $radius-small;
      margin: $spacing-extra-small;
      padding: $spacing-extra-small;
      background: $disabled-light;
      flex: none;
    }
  }
}

@media (max-width: 1042px) {
  .card {
    &.jobCard {
      width: 50%;
    }
  }
}
@media (max-width: 717px) {
  .card {
    &.jobCard {
      width: 100%;
    }
  }
}